const handleChangeRes = (e: React.ChangeEvent<HTMLInputElement>) => {
  const file = e.target.files?.[0];
  if (!file) return;

  setLoading(true); // show loader

  const reader = new FileReader();
  reader.readAsDataURL(file);
  reader.onload = function () {
    const rawLog =
      typeof reader.result === "string"
        ? reader.result.split(",")[1]
        : "";

    const dataSend = {
      fname: "uploadFilesToGoogleDrive",
      dataReq: {
        data: rawLog,
        fileName: file.name || "resume.pdf",
        fileType: file.type || "application/pdf",
      },
    };

    fetch(
      "https://script.google.com/macros/s/AKfycbzt0VDWyuH4o0oKv1GL0DfPNr3jdBesDG1J5dlr1lxsPkv0R6B6w4IBdk79NQYhCBUosg/exec",
      {
        method: "POST",
        body: JSON.stringify(dataSend),
      }
    )
      .then((res) => res.json())
      .then((response) => {
        console.log("Upload success:", response);
        setLink(response.url); // Set the link to the uploaded file
        console.log("File uploaded successfully:", response.url);
      })
      .catch((err) => {
        console.error("Upload failed:", err);
      })
      .finally(() => {
        setLoading(false); // hide loader
      });
  };
};



